<template>
  <div class="account-index">
    <div class="crumbs-box ">
      <span class="crumbs">帐号管理</span>
      <span class="crumbs line">/</span>
      <span class="crumbs">帐号列表</span>
    </div>
    <div class="content-page">
      <div class="table-header">
        <div class="table-header-left">
          <div class="search-form-box">
            <label for="" class="search-label">店铺名称</label>
            <input type="text" v-model="store_name" class="admin-input" placeholder="填写店铺名称" style="width: 250px;">
          </div>
          <div class="search-form-box">
            <label for="" class="search-label">角色</label>
            <el-select v-model="group_id" placeholder="请选择所属商圈">
              <el-option
                v-for="(item, index) in roleList"
                :key="index"
                :label="item.name"
                :value="item.id">
              </el-option>
            </el-select>
          </div>
          <div class="search-form-box">
            <label for="" class="search-label">帐号名称</label>
            <input type="text"  v-model="user_name" class="admin-input" placeholder="填写帐号名称" style="width: 250px;">
          </div>
          <div class="admin-btn flex-row search-btn hover" @click="searchData"><img src="../../assets/ic_shousuo.png" alt="">搜索</div>
        </div>
        <div class="table-header-right">
          <div class="add-btn hover" @click="addAccountLayer()">添加帐号</div>
        </div>
      </div>
      <div class="admin-table" style="width: 100%">
        <el-table :data="tableData" style="width: 100%" :header-cell-style="tableHeaderStyle">
          <el-table-column prop="id" label="序号" width="80"></el-table-column>
          <el-table-column prop="user_name" label="帐号名" width=""></el-table-column>
          <el-table-column prop="business_name" label="昵称" width=""> </el-table-column>
          <el-table-column prop="name" label="角色" width=""></el-table-column>
          <el-table-column prop="store_name" label="所属店铺" width="300"></el-table-column>
          <el-table-column prop="business_status" label="是否启用" width="">
            <template slot-scope="scope">
              <div>
                <el-switch
                  style="display: block"
                  v-model="scope.row.business_status==1?true:false"
                  active-color="#EE5567"
                  inactive-color="#999999"
                  @change="isStartUsing(scope)">
                </el-switch>
              </div>
            </template>
          </el-table-column>
          <el-table-column prop="create_time" label="创建时间" width=""></el-table-column>
          <el-table-column label="操作" width="150">
            <template slot-scope="scope">
              <div class="table-btn hover2" @click="editAccountLayer(scope.row.id)">编辑</div>
              <div class="table-btn hover2" @click="detailsAccountLayer(scope.row.id)">查看</div>
            </template>
          </el-table-column>
        </el-table>
        <div class="page-box" v-if="tableData.length>0">
          <el-pagination
            @current-change="handleCurrentChange"
            background
            layout="prev, pager, next"
            :total="totalPage">
          </el-pagination>
        </div>
      </div>

    </div>
    <!-- layer 添加 编辑 -->
    <div class="layer-box-page " id="addEditAccountLayer">
      <div class="scroll-box">
        <div class="layer-content-page" style="width: 800px;">
          <div class="layer-title-box">
            <div class="title">{{layerTitle}}</div>
            <img src="../../assets/layer_close_icon.png" class="layer-close-icon hover" @click="closeLayer">
          </div>
          <div class="layer-content box-sizing">
            <div class="content" style="margin-top: 0;">
              <form class="form-box">
                <input type="hidden" name="id" v-model="form.id">
                <div class="form-item">
                  <label for="" class="form-label">
                    <span class="require">*</span>
                    <span class="label">帐号</span>
                  </label>
                  <div class="form-input-box">
                    <input type="text" class="admin-input form-input" v-model="form.user_name" :disabled="form.id?true:false" placeholder="请输入账号名，推荐使用手机号码或邮箱">
                  </div>
                </div>
                <div class="form-item">
                  <label for="" class="form-label">
                    <span class="require">*</span>
                    <span class="label">帐号昵称</span>
                  </label>
                  <div class="form-input-box">
                    <input type="text" class="admin-input form-input" v-model="form.nickname" placeholder="请输入账号昵称，如：张三">
                  </div>
                </div>
                <div class="form-item">
                  <label for="" class="form-label">
                    <span class="require" v-if="!form.id">*</span>
                    <span class="label">设置账号初始密码</span>
                  </label>
                  <div class="form-input-box">
                    <input type="password" class="admin-input form-input" v-model="form.password" placeholder="请设置账号初始密码">
                    <div class="form-tips">* 账号密码支持8-16位字母和数字组合</div>
                  </div>
                </div>
                <div class="form-item">
                  <label for="" class="form-label">
                    <span class="require">*</span>
                    <span class="label">所属店铺</span>
                  </label>
                  <div class="form-input-box">
                    <el-select class="form-select" v-model="form.store_id" filterable placeholder="请选择所属店铺">
                      <el-option
                        v-for="(item, index) in storeList"
                        :key="index"
                        :label="item.store_name"
                        :value="item.id">
                      </el-option>
                    </el-select>
                  </div>
                </div>
                <div class="form-item">
                  <label for="" class="form-label">
                    <span class="require">*</span>
                    <span class="label">角色</span>
                  </label>
                  <div class="form-input-box">
                    <el-select class="form-select" v-model="form.group_id" placeholder="请选择角色">
                      <el-option
                        v-for="(item, index) in roleList"
                        :key="index"
                        :label="item.name"
                        :value="item.id">
                      </el-option>
                    </el-select>
                  </div>
                </div>
                <div class="form-item">
                  <label for="" class="form-label">
                    <span class="require">*</span>
                    <span class="label">是否开启</span>
                  </label>
                  <div class="form-input-box">
                    <div class="flex-row" style="height: 50px;">
                      <div :class="['is-switch hover', form.status==1?'active':'']" @click="selectIsSwitch(1)">
                        <img src="../../assets/ic_xuanzhong.png" class="radio-icon" v-if="form.status==1">
                        <img src="../../assets/ic_weixuanzhong.png" class="radio-icon" v-else>
                        <span>是</span>
                      </div>
                      <div :class="['is-switch hover', form.status==2?'active':'']" @click="selectIsSwitch(2)">
                        <img src="../../assets/ic_xuanzhong.png" class="radio-icon" v-if="form.status==2">
                        <img src="../../assets/ic_weixuanzhong.png" class="radio-icon" v-else>
                        <span>否</span>
                      </div>
                    </div>
                  </div>
                </div>
                <div class="form-item">
                  <div class="form-box-btn">
                    <div class="form-btn hover" @click="onSubmitAdd" v-if="!form.id">创建</div>
                    <div class="form-btn hover" @click="onSubmitAdd" v-else>确定</div>
                  </div>
                </div>
              </form>
            </div>
          </div>
        </div>
      </div>
    </div>
    <!-- layer 查看 -->
    <div class="layer-box-page " id="accountDetailsLayer">
      <div class="scroll-box">
        <div class="layer-content-page" style="width: 800px;">
          <div class="layer-title-box">
            <div class="title">查看帐号</div>
            <img src="../../assets/layer_close_icon.png" class="layer-close-icon hover" @click="closeLayer">
          </div>
          <div class="layer-content box-sizing" v-if="detailsInfo">
            <div class="content" style="width: 500px;margin: 0 auto;">
              <form class="form-box">
                <div class="form-item">
                  <label class="form-label">
                    <span class="require">*</span>
                    <span class="label">帐号</span>
                  </label>
                  <div class="form-input-box">
                    <div class="details-text">{{detailsInfo.user_name}}</div>
                  </div>
                </div>
                <div class="form-item">
                  <label class="form-label">
                    <span class="require">*</span>
                    <span class="label">帐号昵称</span>
                  </label>
                  <div class="form-input-box">
                    <div class="details-text">{{detailsInfo.business_name}}</div>
                  </div>
                </div>
                <div class="form-item">
                  <label class="form-label">
                    <span class="require">*</span>
                    <span class="label">账号初始密码</span>
                  </label>
                  <div class="form-input-box">
                    <div class="details-text">**********</div>
                  </div>
                </div>
                <div class="form-item">
                  <label class="form-label">
                    <span class="require">*</span>
                    <span class="label">所属店铺</span>
                  </label>
                  <div class="form-input-box">
                    <div class="details-text">{{detailsInfo.store_name}}</div>
                  </div>
                </div>
                <div class="form-item">
                  <label class="form-label">
                    <span class="require">*</span>
                    <span class="label">角色</span>
                  </label>
                  <div class="form-input-box">
                    <div class="details-text">{{detailsInfo.name}}</div>
                  </div>
                </div>
                <div class="form-item">
                  <label class="form-label">
                    <span class="require">*</span>
                    <span class="label">是否开启</span>
                  </label>
                  <div class="form-input-box">
                    <div class="details-text" v-if="detailsInfo.business_status==1">开启</div>
                    <div class="details-text" v-else>关闭</div>
                  </div>
                </div>
                <div class="form-item">
                  <div class="form-box-btn">
                    <div class="form-btn hover" @click="closeLayer">返回</div>
                  </div>
                </div>
              </form>

            </div>
          </div>
        </div>
      </div>
    </div>


  </div>
</template>
<script>
  import { AdminData, Userstatus, Rulelist, Storelist, UserAdd, Userche, UserEdit } from '@/axios/api'
  import { mapState } from 'vuex'
  export default {
    name: 'account-index',
    data() {
      return {
        token: sessionStorage.getItem('token'),
        totalPage: 1,
        page: 1,
        layerTitle: '创建帐号',
        tableHeaderStyle: {
          'background': '#FAFAFA',
          'height': '76px'
        },
        // search
        store_name: '',
        group_id: '',
        user_name: '',
        //
        tableData: [],
        roleList: [],
        storeList: [],
        form: {
          id: '',
          user_name: '',
          nickname: '',
          password: '',
          store_id: '',
          group_id: '',
          status: 1
        },
        detailsInfo: null,
        userinfo: null
      }
    },
    computed: {
      ...mapState(["host"])
    },
    components: {},
    mounted() {
      this.$store.state.navActive =  this.$route.query.navActive;
      this.userinfo = this.$store.state.userinfo;
      setTimeout(function(){
        $(".el-input__inner").css({
          width: '250px',
          height: '50px'
        });
        $(".form-select .el-input__inner").css({
          width: '400px',
          height: '50px'
        })
      }, 10);
      this.getData();
      this.getRuleList();
      this.getStoreList();
    },
    methods: {
      getData() {
        AdminData({
          token: this.token,
          page: this.page,
          store_name: this.store_name,
          group_id: this.group_id,
          user_name: this.user_name
        }).then(res => {
          if (res.status == 1) {
            let list = res.data.list;
            this.totalPage = res.data.total;
            if (list.length > 0) {
              for(let i in list) {
                list[i].create_time = this.createTime(list[i].create_time)
              }
            }
            this.tableData = list;
          } else {
            this.$message.error(res.msg)
          }
        })
      },
      // 获取角色列表
      getRuleList() {
        Rulelist({
          token: this.token,
          page: 1,
          size: 100
        }).then(res => {
          // console.log(res);
          this.roleList = res.data.list
        })
      },
      // 获取店铺列表
      getStoreList() {
        Storelist({
          token: this.token,
          store_name: '',
          page: 1,
          size: 10000
        }).then(res => {
          this.storeList = res.data.list;
        })
      },
      // 更改状态
      isStartUsing(item) {
        if(this.userinfo.id == item.row.id){
          this.$message.error('不能冻结自己的账号');
          return false;
        }
        let status = this.util.power('2', '4');
        if(status) {
          Userstatus({
            token: this.token,
            user_id: item.row.id,
            type: item.row.business_status == 1 ? 2 : 1
          }).then(res => {
            if (res.status == 1) {
              let tips = item.row.business_status == 1 ? '已禁用' : '已启用';
              this.$message.success(tips);
              this.getData();
            } else {

            }
          })
        } else {
          this.$message.error('暂无权限')
        }

      },
      addAccountLayer() {
        let status = this.util.power('2', '2');
        if (status) {
          this.layerTitle = '创建帐号';
          $("#addEditAccountLayer").fadeIn(100);
        } else {
          this.$message.error('暂无权限')
        }
      },
      editAccountLayer(id) {
        let status = this.util.power('2', '3');
        if (status) {
          Userche({
            token: this.token,
            user_id: id
          }).then(res => {
            if(res.status == 1) {
              let info = res.data;
              this.layerTitle = '编辑';
              this.form = {
                id: id,
                user_name: info.user_name,
                nickname: info.business_name,
                password: '',
                store_id: info.store_id,
                group_id: info.group_id,
                status: info.business_status
              };
              $("#addEditAccountLayer").fadeIn(100);
            } else {
              this.$message.error(res.msg)
            }
          })
        } else {
          this.$message.error('暂无权限')
        }
      },
      // 关闭弹框
      closeLayer() {
        this.form = {
          id: '',
          user_name: '',
          nickname: '',
          password: '',
          store_id: '',
          group_id: '',
          status: 1
        };
        $("#addEditAccountLayer").fadeOut(100);
        $("#accountDetailsLayer").fadeOut(100);
      },
      selectIsSwitch(num) {
        this.form.status = num;
      },
      onSubmitAdd() {
        let formData = this.form;
        if (!formData.user_name) {
          this.$message.error('请输入帐号名');
          return
        }
        if (!formData.nickname) {
          this.$message.error('请输入帐号昵称');
          return
        }
        if (!formData.id) {
          // 新增
          if (!formData.password) {
            this.$message.error('请输入初始密码');
            return
          } else {
            if (formData.password.length < 8 || formData.password.length > 16) {
              this.$message.error('账号密码支持8-16位字母和数字组合');
              return
            }
          }
        } else {
          // 编辑
          if(formData.password.length > 0){
            if (formData.password.length < 8 || formData.password.length > 16) {
              this.$message.error('账号密码支持8-16位字母和数字组合');
              return
            }
          } else {
            delete formData.password
          }
        }
        if(!formData.store_id) {
          this.$message.error('请选择关联店铺');
          return
        }
        if(!formData.group_id) {
          this.$message.error('请选择关联角色');
          return
        }
        formData.token = this.token;
        if (!formData.id) {
          UserAdd(formData).then(res => {
            if(res.status == 1) {
              this.$message.success('添加成功');
              this.closeLayer();
              this.getData();
            } else {
              this.$message.error(res.msg)
            }
          })
        } else {
          formData.user_id = formData.id;
          UserEdit(formData).then(res => {
            if(res.status == 1) {
              this.$message.success('编辑成功');
              this.closeLayer();
              this.getData();
            } else {
              this.$message.error(res.msg)
            }
          })
        }
      },
      // 详情
      detailsAccountLayer(id) {
        let status = this.util.power('2', '1');
        if(status){
          Userche({
            token: this.token,
            user_id: id
          }).then(res => {
            if(res.status == 1) {
              let info = res.data;
              this.detailsInfo = info;
              $("#accountDetailsLayer").fadeIn(100);
            } else {
              this.$message.error(res.msg)
            }
          })
        } else {
          this.$message.error('暂无权限')
        }

      },
      //
      searchData() {
        this.page = 1;
        this.getData();
      },
      // 分页
      handleCurrentChange(curr) {
        // console.log(curr);
        this.page = curr;
        this.getData();
      },
      addTime(m){return m<10?'0'+m:m },
      createTime(value){
        var time = new Date(value*1000);
        var year = time.getFullYear();
        var month = time.getMonth()+1;
        var date = time.getDate();
        var hours = time.getHours();
        var minutes = time.getMinutes();
        var seconds = time.getSeconds();
        return year+'-'+this.addTime(month)+'-'+this.addTime(date)+' '+this.addTime(hours)+':'+this.addTime(minutes)+':'+this.addTime(seconds);
      },//2017-05-08 10:31:27
    }
  }
</script>
<style lang="less" scoped>
  .el-select-dropdown__item {
    font-size: 14px;
    padding: 0 20px;
    position: relative;
    white-space: nowrap;
    overflow: hidden;
    text-overflow: ellipsis;
    color: #606266;
    height: 34px;
    line-height: 34px;
    box-sizing: border-box;
    cursor: pointer;
  }
  .el-select-dropdown__item.selected{
    color: #409EFF !important;
  }
</style>



